.page {
  font-family: "Cinzel", serif;
  scroll-behavior: smooth;
  color: #161616;
  font-size: 16px;
  line-height: 27px;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 100px;

    @include onDesktop {
      margin-bottom: 140px;
    }
  }

  &__section-title {
    margin: 0 0 48px;
    text-align: center;
  }
}

.view {
  font-weight: 700;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: -1.5px;
  text-align: center;
  margin-bottom: 8px;

  @include onDesktop {
    font-size: 48px;
    line-height: 48px;
  }
}
